.cardlist {
  display: flex;
  flex-wrap: wrap;
  padding-right: 70px;
  justify-content: space-between;
}

.row {
  padding: 0;
}
@media screen and (max-width: 670px) {
  .cardlist {
    padding-right: 70px;
  }
}
.cardlist > h1 {
  font-size: medium;
  margin: 180px auto 180px;
}
.loading {
  font-weight: 800;
  text-align: center;
  font-size: medium;
  margin: 220px auto 220px;
}
a {
  text-decoration: none;
}

@media screen and (max-width: 400px) {
  .searchBox {
    padding-left: 15px;
  }
  .searchBox > svg {
    margin-left: 0;
  }
  .searchBox > input {
    margin-left: 2px;
    padding-left: 50px;
  }
  .cardlist {
    justify-content: center;
    padding-right: 0;
  }
}
