.country-card {
  width: 278px;
  margin-top: 30px;
  border-radius: 7px;
  padding: 0;
  padding-bottom: 18px;
  box-shadow: rgba(17, 21, 23, 0.3) 1px 1px 10px;
  cursor: pointer;
}
.country-card:hover {
  transform: scale(1.03);
}

.country-card > img {
  height: 200px;
  border-radius: 7px 7px 0px 0px;
  margin-bottom: 18px;
  width: 100%;
}

.card-body {
  padding: 10px 20px;
}

.name {
  font-weight: 800;
  font-size: 1.1rem;
  margin-bottom: 20px;
}
.secondry {
  font-size: 0.9rem;
  font-weight: 600;
  margin-bottom: 7px;
}

.card-content {
  margin-bottom: 20px;
}

@media screen and (max-width: 400px) {
  .country-card {
    margin-top: 100px;
    transform: scale(1.2);
  }
}

@media screen and (min-width: 1440px) {
  .country-card {
    width: 320px;
  }
}
@media screen and (max-width: 710px) {
  .country-card {
    width: 250px;
  }
  .country-card > img {
    height: 150px;
  }
}

.bdl_c {
  border-bottom: 2px solid rgba(128, 128, 128, 0.1);
}
