footer {
  padding: 20px 70px;
  display: flex;
  justify-content: space-between;
  box-shadow: 1px 1px 8px hsla(200, 15%, 8%, 0.5);
  margin-top: 50px;
}

.dribbble-icon:hover {
  color: #ea4c89;
}
.twitter-icon:hover {
  color: #1da1f2;
}
.github-icon:hover {
  color: black;
}

footer > p {
  margin-top: 10px;
}

@media screen and (max-width: 400px) {
  footer {
    flex-direction: column;
    text-align: center;
    justify-content: center;
  }
  .contact-links {
    justify-content: center;
  }
}
