nav {
  padding: 20px 70px;
  display: flex;
  justify-content: space-between;
  box-shadow: 1px 1px 8px hsla(200, 15%, 8%, 0.5);
}

nav > p {
  font-weight: 600;
  margin-top: 0.5rem;
}
nav > p :hover {
  cursor: pointer;
}
span > svg {
  margin: 0 8px 2px 2px;
}

h1 {
  font-family: Nunito Sans;
  font-weight: 800;
  font-size: 1.3em;
  margin: 0.5rem;
  margin-left: 0rem;
}
@media screen and (max-width: 400px) {
  h1 {
    font-size: 1rem;
  }
  nav > p {
    font-size: 0.8rem;
  }
  nav {
    padding: 20px 20px;
  }
}
