:root {
  --DarkBlue: #2b3945;
  --VeryDarkBlue: #202c37;
  --VeryDarkBlueShadow: #111517;
  --DarkGray: #858585;
  --VeryLightGray: #adadad;
  --White: #ffffff;
}

* {
  font-family: Nunito Sans;
}
.App {
  width: 100%;
}
.content-box {
  width: 99vw;
  padding-left: 70px;
}

@media screen and (min-width: 1440px) {
  .content-box {
    width: 100vw;
  }
}
@media screen and (max-width: 400px) {
  .content-box {
    padding-left: 0px;
  }
}

.bg1 {
  background-color: var(--VeryDarkBlue);
}
.bg2 {
  background-color: var(--DarkBlue);
}
.fc1 {
  color: #f8f8f8;
}
.fc2 {
  color: var(--VeryLightGray);
}
.bgl_1 {
  background-color: var(--White);
}
.bgl_2 {
  background-color: rgb(248, 248, 248);
}
.fcl_1 {
  color: var(--DarkGray);
}
.fcl_2 {
  color: black;
}
.fsb {
  font-size: 1rem;
}
