.searchBox > input {
  border: none;
  border-radius: 5px;
  height: 60px;
  display: flex;
  justify-content: space-around;
}

.searchBox > input:focus {
  outline: none;
}

.searchBox > input::placeholder {
  color: var(--DarkGray);
}
.searchBox > #icon {
  color: var(--DarkGray);
  position: absolute;
  margin-top: 22px;
  margin-left: 22px;
}
.searchContent > select {
  border: none;
  border-radius: 5px;
  height: 60px;
  display: flex;
  justify-content: space-around;
  text-align: center;
  width: 180px;
  outline: none;
  -webkit-appearance: none;
  -moz-appearance: none;
}

.search {
  padding-left: 60px;
  width: 500px;
}

.searchContent {
  margin-top: 50px;
  padding-right: 70px;
}

@media screen and (max-width: 400px) {
  .searchContent {
    margin-top: 15px;
    display: flex;
    flex-direction: column;
  }
  .search {
    margin: 0 auto;
    padding-left: 30px;
    width: 90%;
  }
  #region {
    margin-top: 30px;
    margin-left: 18px;
  }
}
@media screen and (max-width: 850px) {
  .search {
    width: 50vw;
  }
}
@media screen and (max-width: 710px) {
  .search {
    width: 45vw;
  }
  #region {
    width: 25vw;
  }
}
@media screen and (max-width: 670px) {
  .searchContent {
    flex-direction: column;
  }
  .search {
    width: 70vw;
  }
  #region {
    margin-top: 15px;
  }
}

.bdl {
  box-shadow: 0px 0px 10px rgba(173, 173, 173, 0.5);
}
