.backButton {
  border-radius: 5px;
  border: none;
  box-shadow: 1px 1px 5px rgba(17, 21, 23, 0.5);
  margin: 50px 0 0 70px;
  font-size: 0.8rem;
  padding: 10px 25px;
}
.countryData {
  font-size: 0.9rem;
  margin: 50px 0 0 70px;
}
.countryData > img {
  width: 500px;
  height: 367px;
}
.bx {
  box-shadow: 4px 2px 10px rgba(17, 21, 23, 0.5);
}

.countryDetails {
  margin: 30px 0 0 150px;
}
.countryDetails > h2 {
  font-weight: 800;
}
.countryDetails .first-part {
  margin-top: 30px;
  margin-right: 100px;
  line-height: 1;
}
.countryDetails .second-part {
  line-height: 1;
  margin-top: 30px;
}
.borderC {
  margin-top: 50px;
}
.borderC p {
  margin-top: 4px;
}
.countryButton {
  border-radius: 3px;
  height: 1.7rem;
  border: none;
  box-shadow: 1px 1px 5px rgba(17, 21, 23, 0.5);
  font-size: 0.8rem;
  padding: 0 30px;
}

@media screen and (max-width: 450px) {
  .backButton {
    margin-left: 20px;
  }
  .countryData {
    flex-direction: column;
    margin-left: 20px;
  }
  .countryData > img {
    width: 340px;
    height: 250px;
    box-shadow: none;
  }
  .countryDetails {
    margin-left: 0px;
  }
  .countryDetails > div {
    flex-direction: column;
  }
}
